

.jumbotron {

    background-image: url("../../images/socks.png")
}

.navbar {
    background: -webkit-gradient(linear, left top, right top, from(#1b830b) , to(#2f9e1e));
    background: -webkit-linear-gradient(left, #1b830b , #2f9e1e);
    background: -o-linear-gradient(left, #1b830b , #2f9e1e);
    background: linear-gradient(to right, #1b830b , #2f9e1e);
    color: white;

}

a {
    color: white;
}

a:hover {
    color: whitesmoke;
    text-decoration: none;

}

.nav-item {
    padding: 15px 0;
    
}


